@use "global/var" as v;
@use "global/mixins" as m;
@use "sass:color";

.header {
  background-color: v.$Grey;
  padding: 2rem;
  div {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 2rem;
    @include m.desktop {
      flex-direction: row;
      justify-content: space-between;
    }
    h1 {
      margin: 0;
      color: v.$White;
      &:hover {
        color: color.scale(v.$White, $lightness: -10%);
      }
    }

    nav {
      display: flex;
      flex-direction: column;
      gap: 2rem;
      @include m.desktop {
        flex-direction: row;
      }
      a {
        color: v.$White;
        &:hover {
          color: color.scale(v.$White, $lightness: -10%);
        }
      }
    }
  }
}
