@use "global/var" as v;
@use "global/mixins" as m;

.Contact-Box {
  margin-top: 6rem;
  h2 {
    text-align: center;
    color: #4caf50;
    font-weight: bold;
    font-size: 2.5rem;
  }

  .Contact-Data__Full {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5rem;
    @include m.desktop {
      flex-direction: row;
    }

    .Contact-Data__Perfil {
      border-radius: 100%;
      width: 17.5rem;
      height: 20rem;
      @include m.desktop {
        width: 35rem;
        height: 40rem;
      }
    }
  }
}

.Contact-Links {
  .Contact-Link {
    margin-bottom: 2rem;
    a {
      img {
        width: 5rem;
        height: 5rem;
      }
      p {
        color: v.$White;
        &:hover {
          color: v.$LemonGreen;
        }
      }

      display: flex;
      gap: 0.8rem;
    }
  }
}
