@use "global/var" as v;
@use "global/mixins" as m;
@use "sass:color";

main {
  h1 {
    text-align: center;
    color: v.$Green;
  }
}
.Proyectos {
  @include m.desktop {
    @include m.grid(3, 2rem);
  }

  img {
    @include m.desktop {
      height: 30rem;
    }
  }
  .Proyecto {
    margin-bottom: 4rem;
    .Proyecto__info {
      background-color: v.$Grey;
      max-width: 95%;

      padding: 2rem;
      h3 {
        color: v.$Green;
        border-bottom: 1px solid v.$LightGrey;
      }
    }
    .Proyecto__info--links {
      border-top: 1px solid v.$LightGrey;
      padding-top: 1rem;
      display: flex;
      gap: 1rem;
      .Link {
        display: flex;
        flex-direction: column;
        gap: 1rem;
      }
      a {
        color: v.$White;

        &:hover {
          color: color.scale(v.$White, $lightness: -10%);
        }
      }
    }
  }
}

.Proyecto__info--tecnologias {
  ul {
    li {
      margin-bottom: 1rem;
    }
  }
}
