/* Global */
html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto Condensed", sans-serif;
  background-color: #0f0f0f;
  color: #fff;
  margin: 0; /* Elimina el margen predeterminado */
  font-size: 1.5rem;
  line-height: 1.5;
}

h1 {
  font-size: 3rem;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  display: block;
  max-width: 95%;
}

.content {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.meta-dato {
  color: #4caf50;
}

/** Media Queries **/
/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   * ========================================================================== */
/**
   * Correct the font size and margin on `h1` elements within `section` and
   * `article` contexts in Chrome, Edge, Firefox, and Safari.
   */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
   * ========================================================================== */
/**
   * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
   */
dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
   * Remove the margin on nested lists in Edge 18- and IE.
   */
ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
   * 1. Add the correct box sizing in Firefox.
   * 2. Correct the inheritance of border color in Firefox.
   * 3. Show the overflow in Edge 18- and IE.
   */
hr {
  box-sizing: content-box; /* 1 */
  color: inherit; /* 2 */
  height: 0; /* 1 */
  overflow: visible; /* 3 */
}

/**
   * Add the correct display in IE.
   */
main {
  display: block;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
   * ========================================================================== */
/**
   * Remove the gray background on active links in IE 10.
   */
a {
  background-color: transparent;
}

/**
   * Add the correct text decoration in Edge 18-, IE, and Safari.
   */
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
   * Add the correct font weight in Chrome, Edge, and Safari.
   */
b,
strong {
  font-weight: bolder;
}

/**
   * 1. Correct the inheritance and scaling of font size in all browsers.
   * 2. Correct the odd `em` font sizing in all browsers.
   */
code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
   * Add the correct font size in all browsers.
   */
small {
  font-size: 80%;
}

/* Embedded content
   * ========================================================================== */
/**
   * Add the correct display in IE 9-.
   */
audio,
video {
  display: inline-block;
}

/**
   * Add the correct display in iOS 4-7.
   */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
   * Remove the border on images within links in IE 10-.
   */
img {
  border-style: none;
}

/**
   * Hide the overflow in IE.
   */
svg:not(:root) {
  overflow: hidden;
}

/* Tabular data
   * ========================================================================== */
/**
   * 1. Correct table border color inheritance in all Chrome, Edge, and Safari.
   * 2. Remove text indentation from table contents in Chrome, Edge, and Safari.
   */
table {
  border-color: inherit; /* 1 */
  text-indent: 0; /* 2 */
}

/* Forms
   * ========================================================================== */
/**
   * Remove the margin on controls in Safari.
   */
button,
input,
select {
  margin: 0;
}

/**
   * 1. Show the overflow in IE.
   * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
   */
button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
   * Correct the inability to style buttons in iOS and Safari.
   */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
   * Correct the padding in Firefox.
   */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
   * Show the overflow in Edge 18- and IE.
   */
input {
  overflow: visible;
}

/**
   * 1. Correct the text wrapping in Edge 18- and IE.
   * 2. Correct the color inheritance from `fieldset` elements in IE.
   */
legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
   * 1. Add the correct display in Edge 18- and IE.
   * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
   */
progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
   * Remove the inheritance of text transform in Firefox.
   */
select {
  text-transform: none;
}

/**
   * 1. Remove the margin in Firefox and Safari.
   * 2. Remove the default vertical scrollbar in IE.
   */
textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
   * 1. Add the correct box sizing in IE 10-.
   * 2. Remove the padding in IE 10-.
   */
[type=checkbox],
[type=radio] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}

/**
   * 1. Correct the odd appearance in Chrome, Edge, and Safari.
   * 2. Correct the outline style in Safari.
   */
[type=search] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
   * Correct the cursor style of increment and decrement buttons in Safari.
   */
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
   * Correct the text style of placeholders in Chrome, Edge, and Safari.
   */
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
   * Remove the inner padding in Chrome, Edge, and Safari on macOS.
   */
::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
   * 1. Correct the inability to style upload buttons in iOS and Safari.
   * 2. Change font properties to `inherit` in Safari.
   */
::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
   * Remove the inner border and padding of focus outlines in Firefox.
   */
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
   * Restore the focus outline styles unset by the previous rule in Firefox.
   */
:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
   * Remove the additional :invalid styles in Firefox.
   */
:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
   * ========================================================================== */
/*
   * Add the correct display in Edge 18- and IE.
   */
details {
  display: block;
}

/*
   * Add the correct styles in Edge 18-, IE, and Safari.
   */
dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
   * Add the correct display in all browsers.
   */
summary {
  display: list-item;
}

/* Scripting
   * ========================================================================== */
/**
   * Add the correct display in IE 9-.
   */
canvas {
  display: inline-block;
}

/**
   * Add the correct display in IE.
   */
template {
  display: none;
}

/* User interaction
   * ========================================================================== */
/**
   * Add the correct display in IE 10-.
   */
[hidden] {
  display: none;
}

/* Layout */
.Complet-Box {
  margin-top: 4rem;
  padding: 5rem;
  background-color: #312f2f;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
  width: 80%;
}
@media (min-width: 992px) {
  .Complet-Box {
    width: 25%;
  }
}
.Complet-Box .Personal-Data {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Complet-Box .Personal-Data h1 {
  text-align: center;
}
.Complet-Box .Personal-Data img {
  width: 100px;
  height: 100px;
  border-radius: 100%;
}
.Complet-Box .Personal-Data .Location-Data {
  color: #87cf66;
  font-weight: bold;
}
.Complet-Box .Contact-Data .Contact-Data-Btn {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Complet-Box .Contact-Data .Contact-Data-Btn a {
  background-color: #3a3838;
  padding: 1rem;
  display: block;
  width: 100%;
  text-align: center;
  margin-top: 1rem;
  color: #fff;
  font-weight: bold;
  border-radius: 5px;
}
.Complet-Box .Contact-Data .Contact-Data-Btn a:hover {
  background-color: rgb(52.2, 50.4, 50.4);
}

.header {
  background-color: #312f2f;
  padding: 2rem;
}
.header div {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .header div {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header div h1 {
  margin: 0;
  color: #fff;
}
.header div h1:hover {
  color: rgb(229.5, 229.5, 229.5);
}
.header div nav {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .header div nav {
    flex-direction: row;
  }
}
.header div nav a {
  color: #fff;
}
.header div nav a:hover {
  color: rgb(229.5, 229.5, 229.5);
}

.About h2 {
  text-align: center;
}
.About .AboutMe {
  line-height: 2;
}
.About .About-Data {
  text-align: center;
}
.About .About-Data .About-Data__list li {
  margin-top: 1rem;
}
.About .About-Data .About-Data__list li:nth-child(2n) {
  color: #a3eea6;
}
.About h2,
.About h3 {
  color: #4caf50;
  font-weight: bold;
  font-size: 2.5rem;
}

.Contact-Box {
  margin-top: 6rem;
}
.Contact-Box h2 {
  text-align: center;
  color: #4caf50;
  font-weight: bold;
  font-size: 2.5rem;
}
.Contact-Box .Contact-Data__Full {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5rem;
}
@media (min-width: 1024px) {
  .Contact-Box .Contact-Data__Full {
    flex-direction: row;
  }
}
.Contact-Box .Contact-Data__Full .Contact-Data__Perfil {
  border-radius: 100%;
  width: 17.5rem;
  height: 20rem;
}
@media (min-width: 1024px) {
  .Contact-Box .Contact-Data__Full .Contact-Data__Perfil {
    width: 35rem;
    height: 40rem;
  }
}

.Contact-Links .Contact-Link {
  margin-bottom: 2rem;
}
.Contact-Links .Contact-Link a {
  display: flex;
  gap: 0.8rem;
}
.Contact-Links .Contact-Link a img {
  width: 5rem;
  height: 5rem;
}
.Contact-Links .Contact-Link a p {
  color: #fff;
}
.Contact-Links .Contact-Link a p:hover {
  color: #87cf66;
}

.ubicacion {
  margin-bottom: 10rem;
}
.ubicacion h2 {
  text-align: center;
  color: #4caf50;
  font-weight: bold;
  font-size: 2.5rem;
}

main h1 {
  text-align: center;
  color: #4caf50;
}

@media (min-width: 1024px) {
  .Proyectos {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .Proyectos img {
    height: 30rem;
  }
}
.Proyectos .Proyecto {
  margin-bottom: 4rem;
}
.Proyectos .Proyecto .Proyecto__info {
  background-color: #312f2f;
  max-width: 95%;
  padding: 2rem;
}
.Proyectos .Proyecto .Proyecto__info h3 {
  color: #4caf50;
  border-bottom: 1px solid #3a3838;
}
.Proyectos .Proyecto .Proyecto__info--links {
  border-top: 1px solid #3a3838;
  padding-top: 1rem;
  display: flex;
  gap: 1rem;
}
.Proyectos .Proyecto .Proyecto__info--links .Link {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.Proyectos .Proyecto .Proyecto__info--links a {
  color: #fff;
}
.Proyectos .Proyecto .Proyecto__info--links a:hover {
  color: rgb(229.5, 229.5, 229.5);
}

.Proyecto__info--tecnologias ul li {
  margin-bottom: 1rem;
}