@use "global/var" as v;
@use "global/mixins" as m;

.About {
  h2 {
    text-align: center;
  }
  .AboutMe {
    line-height: 2;
  }
  .About-Data {
    text-align: center;
    .About-Data__list {
      li {
        margin-top: 1rem;
        &:nth-child(2n) {
          color: #a3eea6;
        }
      }
    }
  }

  h2,
  h3 {
    color: #4caf50;
    font-weight: bold;
    font-size: 2.5rem;
  }
}
